@import url(https://fonts.googleapis.com/css2?family=Monofett&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  font-family: Monofett;
  font-weight: 400;
  color: #FFF73C;
  font-size: 45px;
  text-shadow: 0 5px 30px #FFF73C;
  margin-top: 5rem;
}

@media (max-width: 400px) {
  .header {
    margin-top: 2rem;
    margin-left: 1rem;
    margin-right: 1rem;
  }
}
.score-container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    height: 50px;
}

.score {
    color: white;
    font-family: sans-serif;
    font-size: 20px;
}
.hint-wrapper {
    position: relative;
    width: 70%;
}

.hint-card {
    height: 15rem;
    width: 15rem;
    position: absolute;
    background-color: rgb(228, 228, 8);
    bottom: 7rem;
    left: 2rem;
    display: flex;
    flex-direction: row;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    border-radius: 14px;
}

.hint-text {
    width: 60%;
    margin-left: 15px;
    font-size: 17px;
}

.closing-btn {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2px;
    margin-left: 2px;
    font-size: 16px;
    background-color: #EB5757;
    color: #FFFF;
}

.closing-btn:hover {
    background-color: #a52525;
}
.hints-left-container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
}

.hints-left {
    color: white;
    font-family: sans-serif;
    font-size: 20px;
}
.final-score {
    color: #fff;
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.answer-body {
    display: flex;
    flex-direction: column; 
    align-items: center;
    position: relative;
}

.game-stats {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.container {
  display: grid; 
  grid-template-columns: 300px;
  grid-template-rows: 1fr 1fr; 
  grid-gap: 15px 0px; 
  gap: 15px 0px; 
}

.emojis {
    font-size: 30px;
    margin-top: 5rem;
}

.result-container {
  height: 3rem;
}

.answer-result {
  color: #FFFFFF;
  font-family: sans-serif;
}

.answerField {
  grid-column: 1;
  grid-row: 1;
  background: rgba(255, 247, 60, 0.3);
  border: 5px solid #EB5757;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  color: #FFFFFF;
  font-family: sans-serif;
  font-size: 20px;
  font-weight: 600;
}

.answerField:focus {
  outline: none;
}

.buttoncheck {
  grid-column: 1;
  grid-row: 2;
  background-color: #FFF73C;
  font-family: Monofett;
  font-size: 35px;
  cursor: pointer;
}

.buttoncheck:hover {
  background-color: #d6d029;
}

.bottombuttons {
  display: grid;
  grid-template-columns: 150px 150px;
  grid-template-rows: 1fr;
}

.hint-btn, .next-button, .hint-btn:disabled:hover {
  background: rgba(255, 247, 60, 0.3);
  font-family: Monofett;
  color: #FFFFFF;
  font-size: 35px;
  cursor: pointer;
}

.hint-btn {
  grid-column: 1 / 2;
  grid-row: 1;
  width: 150px;
}

.next-button {
  grid-column: 2 / 3;
  grid-row: 1;
  width: 100%;
}

.hint-btn:hover, .next-button:hover {
  background-color: #FFF73C;
  color: #000;
}

@media (max-width: 400px) {
  .answer-body {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
  }

  .game-stats {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .answer-result {
    font-size: 20px;
  }

  .next-button:hover {
    background: rgba(255, 247, 60, 0.3);
    color: #FFFFFF;
  }
}
.quiz-description, .welcome-msg {
    color: #fff;
}

.quiz-description {
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-left: 4rem;
    margin-right: 4rem;
}

.start-btn {
    background-color: #FFF73C;
    font-family: Monofett;
    font-size: 35px;
    cursor: pointer;
}
      
.start-btn:hover {
    background-color: #d6d029;
}

.App {
  text-align: center;
  background-color: #171a1f;
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
}

input {
  text-align: center;
}

